 <template>
  <el-main class="main">
    <div class="comName">企业名称： {{comName}}</div>
    <div class="btn-box">
      <el-button @click="addGoods">新增商品</el-button>
      <el-button>商品计划管理</el-button>
    </div>
    <el-table :data="goodsInfoList">
        <el-table-column prop="id" label="ID">
        </el-table-column>
        <el-table-column prop="name" label="商品名称">
        </el-table-column>
        <el-table-column prop="youServers" label="商品包含服务">
        </el-table-column>
        <el-table-column prop="ptGoodsPlanName" label="所属计划">
        </el-table-column>
        <el-table-column prop="beginTime" label="生效时间">
        </el-table-column>
        <el-table-column prop="endTime" label="失效时间">
        </el-table-column>
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="undercarriage(scope.row)">下架</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box" v-show="pageShow">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>
  </el-main>
 </template>

 <script>
 import queryList from '../../utils/queryList'
 export default {
   name: 'goodsManage',
   data() {
     return {
       comName: this.$route.query.comName,
       goodsInfoList: [],
       currentPage: 1,
       pageShow: false
     }
   },
   created() {
     this.init()
   },
   methods: {
     handleClick(item) {
       this.$router.push('addOrEditGoods?type=edit&id=' + item.id)
     },
     undercarriage() {

     },
     addGoods() {
       this.$router.push('addOrEditGoods?type=add')
     },
     handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    init() {
      const companyId = this.$route.query.id
      const params = {
        companyId,
        page: this.currentPage -1,
        size: 20
      }
      queryList('goods', params).then((res) => {
        this.goodsInfoList = res.data.data.list
        this.pageShow = this.goodsInfoList.length > 20
      }).catch((err) => {
        console.log(err)
        // this.$message(err)
      })
    }
  }
 }
 </script>
 
 <style lang="less" scoped>
  .main{
    text-align: left;
  }
  .btn-box{
    padding: 20px 0;
    border-bottom: 1px solid #eee;
  }
  .comName{
    border-bottom: 1px solid #eee;
  }
  .com-input {
    width: 210px;
  }
 </style>
 